* {
    padding: 0;
    margin: 0;
}
*, *:after, *:before {
    box-sizing: border-box;
}

.side-panel {
    position: absolute;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
    font-family: 'TT Lakes', sans-serif;
    width: 320px;
    background-color: white;
    z-index: 100;
}
.header__side-panel {
    width: 320px;
    height: 88px;
    background: white;
    display: flex;
}
.head {
    display: flex;
    margin: 18.73px 24px 18.73px 24px;
}


.side-panel__body {
    width: 220px;
    height: 900px;
    background-color: white;
    margin: 30px 0 0 0;
}
.list_name {
    display: flex;
}
p {
    margin: 4px 0 0 24px;
    color: #7E7E82;
}
.list_name__text {
    font-family: "TT Lakes";
    font-size: 16px;
    line-height: 24px;
}

.content__list {
    display: flex;
    flex-direction: column;
    white-space: nowrap;
    margin-left: 24px;
    color: #1B1C21;
}

.title__button {
    margin-top: 0px;
    width: 4px;
    height: 40px;
    background: #41F6D7;
    border-bottom-right-radius: 2px;
    border-top-right-radius: 2px;
    border: none;
}

.content__list--item {
    margin: 32px 0 0 0;
}
.content__list--item:first-child {
    margin-top: 28px;
}
.contact {
    height: auto;
    display: flex;
    flex-direction: column;
    margin: 268px 0px 0px 24px;
}
.contact__icons {
    margin: 0 16px 16px 0;
}
.contact__phone-number {
    font-family: "TT Lakes";
    font-size: 24px;
    line-height: 32px;
    color: #1B1C21;
    white-space: nowrap;
    margin-bottom: 40px;
}
.contact__email {
    margin: 0px 0px 12px 0px;
}
.language {
    display: flex;
}
.language__ru {
    color: #7E7E82;
    margin-right: 7px;
    margin-bottom: 32px;
}
.language__en {
    color: #1B1C21;
    margin-right: 7px;
}
.language__ch {
    color: #1B1C21;
    margin-right: 7px;
}