/* иконки начинаются */
.menu-icon__background {
    width: 40px;
    height: 40px;
    background: #FF3E79;
    border-radius: 20px;
    border: 0px;
}

.menu-icon__background:hover {
    transition-duration: 300ms;
    opacity: 0.3;
    transform: scale(0.9);
}

.menu-icon__call {
    background-image: url(./static/call.svg);
    background-repeat: no-repeat;
    background-position: center;
}
.menu-icon__chat {
    background-image: url(./static/chat.svg);
    background-repeat: no-repeat;
    background-position: center;
}
.menu-icon__profile {
    background-image: url(./static/profile.svg);
    background-repeat: no-repeat;
    background-position: center;
}
.menu_icon__burger {
    background-image: url(./static/burger.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-left: 0;
    margin-right: 16px;
}
.menu-icon__search {
    background-image: url(./static/looks.svg);
    background-repeat: no-repeat;
    background-position: center;
    align-content: flex-end;
    margin-right: 0;
}
.menu-icon__settings {
    background-image: url(./static/repair.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-left: 20px;
    margin-right: 16px;
}
.menu-icon__line {
    width: 2px;
    height: 32px;
    background: #EAEAEA;
    border-radius: 1px;
    margin-left: 0px;
    margin-right: 0px;
}
.menu-icon__line_first {
    background: #EAEAEA;
    border-radius: 1px;
    width: 2px;
    height: 32px;
    margin: 20px;
    margin-left: 4px;
}

.menu-icon__CPS {
    margin-right: auto;
    background-image: url(./static/Group.svg);
    width: 96.55px;
    height: 50.81px;
}

.menu-icon__cancel {
    background-image: url(./static/cancel.svg);
    width: 40px;
    background-position: center;
    background-repeat: no-repeat;
    margin-right: 16px;
}

.menu-icon {
    width: 100%;
    display: flex;
    padding-right: 16px;
    padding-left: 16px;
    align-items: center;
    border-bottom: 1px solid #D9FFF5;
}

/* иконки кончаются */
