.swiper {
    width: 280px;
    height: 120px;
}
.swiper__position {
    width: auto;

}


.swiper-slide {

    width: 240px;
    height: 72px;
    border: 1px solid #EAEAEA;
    border-radius: 6px;
}

.slide__lenovo {
    background: url(./static/lenovo.svg) no-repeat;
    background-position: 16px 50%;
    background-color: white;
    margin-right: 16px;
    width: 240px;
}
