.swiper__button {
    width: 40px;
    height: 40px;
    border: 2px solid #FF3E79;
    background: url(./static/redArrow.svg) no-repeat;
    background-position: 50% 50%;
    border-radius: 50%;
    margin-left: auto;
    margin-top: 20px;
    margin-right: 16px;
}